  // constructor
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    public menuCtrl: MenuController,
    public modalCntrl: ModalController,
    public nav: NavController,
    private navCtrl: NavController,
    private events: Events,
    public statusbar: StatusBar,
    private elementRef: ElementRef,
    private articleService: ArticleService,
    private storage: Storage
  ) {
    //for status bar
    this.initializeApp();
    this.itemsHome = [
      { expandedHome: false },
    ];
    this.itemsCategory = [
      { expandedcategory: false },
    ];
    this.itemsShop = [
      { expandedShop: false },
    ];

    this.itemsList = [
      { expandedHelp: false },
    ];
    this.itemsGrid = [
      { expandedHelp: false },
    ];
    this.itemsCard = [
      { expandedHelp: false },
    ];
    this.itemsSlider = [
      { expandedHelp: false },
    ];
    this.itemsTab = [
      { expandedHelp: false },
    ];
    this.itemsSegment = [
      { expandedHelp: false },
    ];
    this.itemsButton = [
      { expandedHelp: false },
    ];
    this.itemsRadio = [
      { expandedHelp: false },
    ];
    this.itemsCheckbox = [
      { expandedHelp: false },
    ];
    this.itemsBadge = [
      { expandedHelp: false },
    ];
    this.itemsToogle = [
      { expandedHelp: false },
    ];
    this.itemsSearchbar = [
      { expandedHelp: false },
    ];
    this.itemsFab = [
      { expandedHelp: false },
    ];
    this.itemsAlert = [
      { expandedHelp: false },
    ];
    this.itemsToast = [
      { expandedHelp: false },
    ];
    this.itemsActionsheet = [
      { expandedHelp: false },
    ];
    this.itemsForm = [
      { expandedHelp: false },
    ];
    this.itemColor = ["#F44336"];
    this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
    this.events.subscribe('icons', (data) => {
      this.iconColorVar = data;
      if (data == "autumn") {
        this.itemColor = ["#F44336"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "night") {
        this.itemColor = ["#673AB7"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "neon") {
        this.itemColor = ["#03A9F4"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "orginal") {
        this.itemColor = ["#4CAF50"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "red") {
        this.itemColor = ["#9E9E9E"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "purple") {
        this.itemColor = ["#E91E63"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "Lightblue") {
        this.itemColor = ["#3F51B5"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "Lightgreen") {
        this.itemColor = ["#00BCD4"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "Lightgray") {
        this.itemColor = ["#8BC34A"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
      else if (data == "blue") {
        this.itemColor = ["#008577"];
        this.elementRef.nativeElement.style.setProperty('--my-var', this.itemColor);
      }
    });
  }

