  /////
  ////////////
  expandItemList(item): void {
    this.visibleList = !this.visibleList;
    if (item.expandedHelp) {
      item.expandedHelp = false;
    } else {
      this.itemsList.map(listItem => {
        if (item == listItem) {
          listItem.expanded = !listItem.expanded;
        } else {
          listItem.expanded = false;
        }
        return listItem;
      });
    }
  }
